body{
    background-color: #AFEEEE;
}
.container{
    background-color: cornflowerblue;
    width: 95%;
    #margin: auto;
    float: left;
    margin-top: 7%;
}
.chat-window {
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 25px;
    height: 75vh;
    margin: 20px;
    #overflow-y: scroll;
    padding-top: 15%;
    text-align: center;
    background-image: url("./background.jpg");
}

.chat-window h2 {
    font-size: 2.5rem;
    margin-bottom: 10px;
    color: dodgerblue;
}

.chat-input {
    display: flex;
    flex-direction: revert;
    position: static;
    height: 35px;
    padding-top: 3vw;
    margin-right: 5vw;
    margin-left: 5vw;
    flex: 1;
    border: none;
    border-radius: 5px;
}

.chat-input input[type="text"] {
    flex-grow: 1;
    margin-right: 10px;

    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #f0f0f0;

}
#message{
    flex: 1;
    border: none;
    border-radius: 20px;
    padding: 10px;
    margin-right: 10px;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.chat-input button {
    background-color: #0077cc;
    border: none;
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.chat-input button:hover {
    background-color: #005fa3;
    cursor: pointer;
}
.info{
    font-weight: bold;
}
.chat-window h4 {

    margin-bottom: 10px;
    color: dodgerblue;
}
.chatbot{
    height: 45vh;
    overflow:auto;
    width: 80%;
    margin-left: 10%;
    border: 2px solid dodgerblue;;
    padding: 2px;
    /*841*/
}
.userMsg {
    background-color: #0077cc;
    align-self: flex-end;
    text-align: center;
    width: 45%;
    line-height: 25px;
    margin-left: auto;
    margin-right: auto;
    #margin: 10px;
    #padding: 4px;
    #max-width: 180%;
    border-radius: 8px;
    float: right;
}

.chatMsg{
    background-color: grey;
    align-self: flex-start;
    text-align: center;
    width: 45%;
    margin-left: auto;
    margin-right: auto;
    #margin: 10px;
    #padding: 10px;
    /*max-width: 80%;*/
    border-radius: 8px;
    line-height: 25px;
    float: left;
}
.message {
    margin: 8px;
    padding: 16px;
    border-radius: 5px;
    max-width: 93%;
    clear: both;
}
.history{
    background-color: cornflowerblue;
    border: 2px solid #ccc;
    height: 560px;
    margin: 10px;
    padding-right: 40px;
    padding-top: 1%;
    padding-left: 40px ;
    text-align: center;
    float: left;
    width: 75%;
    z-index: 1;

}
.history_user{
    overflow-y: scroll;
    border-radius: 15px;
    background-color: #f5f5f5;
    height: 340px;
    padding: 5px;
    margin: 5px;
    background-image: url("./kratka.jpg");
}
.help_user{
    background-color: lightslategray;
    border-radius: 15px;
    height: 180px;
    padding: 5px;
    margin: 5px;
    text-align: justify;

}
.help_user h4{
    text-align: center;
}
