.Card1_r
{
    margin-top: 12%;
    height:100vh;
    font-weight: bold;
}

.button1_r{
    margin: 10px 0 0 0;
    padding: 7px 10px;
    borderRadius: 3px;
    background: linear-gradient(to bottom right, greenyellow, green);
    width: 100%;
    fontSize: 20px;
    color: Black;
    display: block;
    font-weight: bold;
}
.button1_r:hover{
    cursor: pointer;
    background: linear-gradient(to bottom right, lawngreen, palegreen);
}
.formStyle1_r  {
    margin: 100px auto;
    padding: 20px;
    border: 1px solid #c9c9c9;
    borderRadius: 5px;
    background: linear-gradient(to bottom right, #bf80ff, #809fff);
    color:white;
    width: 65%;

}
.labelStyle1_r  {
    fontFamily: Arial, Helvetica, sans-serif;
    fontSize: 25px;
}

.inputStyle1_r  {
    width: 100%;
    border: 1px solid #bfbfbf;
}

.Card1_r .formHeader_r{
    margin-bottom: -5%;
    text-align: center;
    font-size: 50px;
}
.h3{
    font-size: 12px;
}
.NoLog_r{
    text-align: center;
}
